export default {
		formatDate(date) {
			let old = new Date(date)
			let now = new Date()
			let h = old.getHours()
			let m = old.getMinutes()
			let Y = old.getFullYear()
			let M = old.getMonth() + 1
			let D = old.getDate()
			
			let nh = now.getHours()
			let nm = now.getMinutes()
			let nY = now.getFullYear()
			let nM = now.getMonth() + 1
			let nD = now.getDate()
			
			if(Y === nY && M === nM && D === nD) {
				h = h < 10 ? '0' + h : h;
				m = m < 10 ? '0' + m : m;
				return h + ':' + m
			}
			if(Y === nY && M === nM && D + 1 === nD) {
				h = h < 10 ? '0' + h : h;
				m = m < 10 ? '0' + m : m;
				return '' + h + ':' + m
			}
			else {
				M = M < 10 ? '0' + M : M;
				D = D < 10 ? '0' + D : D;
				return Y + '-' + M + '-' + D
				}
		},
		// 
		time_interval(old, now) {
			old = new Date(old);
			now = new Date(now);
			// console.log("old:" + old + "now:" + now)
			let n_old = old.getTime();
			let n_now = now.getTime()
			if(n_old > (n_now + 1000 * 60 * 2)) {
				return now
			}else {
				return ''
			}
		},
		// 
		formatDate_chat(date) {
			let old = new Date(date)
			let now = new Date()
			let h = old.getHours()
			let m = old.getMinutes()
			let Y = old.getFullYear()
			let M = old.getMonth() + 1
			let D = old.getDate()
			
			let nh = now.getHours()
			let nm = now.getMinutes()
			let nY = now.getFullYear()
			let nM = now.getMonth() + 1
			let nD = now.getDate()
			
			if(Y === nY && M === nM && D === nD) {
				h = h < 10 ? '0' + h : h;
				m = m < 10 ? '0' + m : m;
				return h + ':' + m
			}
			if(Y === nY && M === nM && D + 1 === nD) {
				h = h < 10 ? '0' + h : h;
				m = m < 10 ? '0' + m : m;
				return '' + h + ':' + m
			}
			//
			else if(Y === nY) {
				M = M < 10 ? '0' + M : M;
				D = D < 10 ? '0' + D : D;
				h = h < 10 ? '0' + h : h;
				m = m < 10 ? '0' + m : m;
				return M + '' + D + ' ' + h + ':' + m
				}
			else {
				M = M < 10 ? '0' + M : M;
				D = D < 10 ? '0' + D : D;
				return Y + '' + M + '' + D + ''
				}
		}
	
}